import Home from './pages/public/Home'
import {Roles} from './modules/session'
export default [
  { path: '/', name: 'home', component: Home, meta: {role:Roles.public} },
  { path: '/login',name: 'login', component: resolve => require(['./pages/public/Login.vue'], resolve), meta: {role:Roles.public} },
  { path: '/signup',name: 'signup', component: resolve => require(['./pages/public/Signup.vue'], resolve), meta: {role:Roles.public} },
    { path: '/thankyou',name: 'thankyou', component: resolve => require(['./pages/public/Thankyou.vue'], resolve), meta: {role:Roles.public} },
  { path: '/restrict', name: 'restrict', component: resolve => require(['./pages/public/restrict.vue'], resolve), meta: {role:Roles.public} },
  { path: '/customer/products', name: 'customer.products', component: resolve => require(['./pages/customer/products.vue'], resolve), meta: {role:Roles.customer} },
   { path: '*', name: 'notfount', component: resolve => require(['./pages/public/notfound.vue'], resolve), meta: {role:Roles.public} }
]



