import message from '../components/Message'
import modal from '../components/Modal'
import sidebar from '../components/Sidebar'
import {Roles,login, logout, getUser, isAuth, isAllowed, isTokenExpired} from '../modules/session'
import RPC from  '../modules/server'

let tools = {}
tools.install = function (Vue, options) {
  // 1. add global method or property
  Vue.myGlobalMethod = function () {
    // something logic ...
    console.log('Global')
  }
  // 2. add a global asset
  Vue.directive('my-directive', {
    bind(el, binding, vnode, oldVnode) {
      // something logic ...
    }

  })

  // 3. inject some component options
 Vue.component('message', message);
 Vue.component('modal', modal);
 Vue.component('sidebar', sidebar);

// mixin
  Vue.mixin({
     data () {
    return {
      Roles: Roles
    }
  },
    created: function () {
      // something logic ...
    }

  })
  // 4. add an instance method
  Vue.prototype.$server = {}
  Vue.prototype.$server.rpc = RPC
  Vue.prototype.$getUser = getUser
  Vue.prototype.$login = login
  Vue.prototype.$logout = logout
  Vue.prototype.$isAuth = isAuth
  Vue.prototype.$isAllowed = isAllowed
  Vue.prototype.$isTokenExpired = isTokenExpired

}

export default tools



