export default function Validators(validator) {

  validator.extend('password', {
    getMessage: field => `${field} must have 6 to 15 characters and contain 1 uppercase letter and 1 digit.`,
    validate: value => {
return /^(?=.*[A-Z])(?=.*[0-9])(?=.{6,15}$)/.test(value);
/*      return new Promise(function(resolve, reject) {
      if (value.match(/^(?=.*[A-Z])(?=.*[0-9])(?=.{6,15}$)/) ){
        resolve(true)
      }  else {
        reject(false)
      }
    })*/

    }
  }) // Password

  validator.extend('loginname', {
    getMessage: field => `${field} must have 6 to 20 characters.Can contain A-Z, a-z and 0-9.`,
    validate: value => {return /^[a-zA-Z0-9]{6,20}$/.test(value);}
}) // Loginin name

validator.extend('terms', {
  getMessage: field => 'You must agree with terms and conditions.',
  validate: value => {return value}
}) // Loginin name

}



