<template>
<div id="app" class="ui container">
  <section id="header" class="ui equal width padded grid">
    <div class="row toolbar">
      <div class="column">
        <a @click="$router.push('/')">
          <!--   <img src="./assets/logo.svg" class="icon" align="absmiddle"> -->
          <div class="logo">Restock<i class="registered icon"></i></div>
        </a>
        <button class="ui circular basic icon button menubtn" @click.stop="onMenu">
  <i class="content icon"></i> <span>Menu</span>
</button>
      </div>
      <div class="right aligned column">
        <div v-if="isLoggedIn">
          <button class="ui blue button" @click="onSignout">Sign Out</button>
        </div>
        <div v-else>
          <span>Already a customer? </span>
          <button class="ui blue button" @click="$router.push('/login')">Login</button>
        </div>
      </div>
    </div>
  </section>
  <section id="contents" class="ui grid">

    <div class="sixteen wide column">
      <sidebar ref="sidebar">
        <div class="ui inverted vertical menu">
          <a v-for="item in menuItems" class="item" :name="item.name" @click="$router.push(item.link)">
            <i class="icon" :class="[item.icon]"></i> {{item.label}}
          </a>
        </div>
      </sidebar>
   <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
    </div>

  </section>

</div>
</template>

<script>
'use strict';
export default {
  data() {
    return {
      menuItems: [],
      isMenu: false,
      isLoggedIn: false,
      user: {}
    }
  },
  created() {
    if (this.$isTokenExpired()) {
      this.user = {};
      this.isLoggedIn = false;
      this.setMenu();
    } else {
      this.user = this.$getUser().user;
      this.isLoggedIn = true;
      this.setMenu(this.user.role);
    }

    this.$root.$on('onLogin', (e) => {
      this.user = this.$getUser().user;
      this.isLoggedIn = true;
      this.setMenu(this.user.role);
      switch (this.user.role) {
        case this.Roles.admin:
          this.$router.push('admin.areas')
          break;
        case this.Roles.customer:
          this.$router.push({name: 'customer.products'});
          break;
        case this.Roles.courier:
          this.$router.push('courier.areas');
          break;
      }
    })
  },

  destroyed() {
    this.$root.$off('onLogin')
  },
  methods: {
    onMenu() {
      if (this.isMenu) {
        this.$refs.sidebar.hide()
        this.isMenu = false
      } else {
        this.$refs.sidebar.show();
        this.isMenu = true
      }
    },
    setMenu(role) {
      switch (role) {
        case this.Roles.admin:
          this.menuItems = [{
              name: 'customers',
              link: '/admin/customers',
              label: 'Customers',
              icon: 'shopping-cart'
            }, {
              name: 'products',
              link: '/admin/products',
              label: 'Products',
              icon: 'shopping-cart'
            }, {
              name: 'billing',
              link: '/admin/billing',
              label: 'Billing',
              icon: 'shopping-cart'
            }, {
              name: 'invoices',
              link: '/admin/invoices',
              label: 'Invoices',
              icon: 'shopping-cart'
            }

          ];
          break;
        case this.Roles.customer:
          this.menuItems = [{
              name: 'subscriptions',
              link: '/customer/subscriptions',
              label: 'Subscriptions',
              icon: 'unhide'
            }, {
              name: 'products',
              link: '/customer/products',
              label: 'Products',
              icon: 'barcode'
            }, {
              name: 'requestions',
              link: '/customer/requisitions',
              label: 'Requestions',
              icon: 'payment'
            }, {
              name: 'invoices',
              link: '/customer/invoices',
              label: 'Invoices',
              icon: 'shop'
            }, {
              name: 'settings',
              link: '/customer/settings',
              label: 'Settings',
              icon: 'setting'
            }

          ];
          break;
        case this.Roles.courier:
          this.menuItems = [{
              name: 'arealist',
              link: '/courier/areas',
              label: 'Area List',
              icon: 'shopping-cart'
            }, {
              name: 'customerlist',
              link: '/courier/customers/all',
              label: 'Customer List',
              icon: 'shopping-cart'
            }

          ];
          break;
        default:
          this.menuItems = [{
              name: '',
              link: '/',
              label: 'Home',
              icon: 'home'
            }, {
              name: 'signup',
              link: '/signup',
              label: 'Create free account',
              icon: 'verified-user'
            }, {
              name: 'Sigin',
              link: '/login',
              label: 'Login',
              icon: 'exit-to-app'
            }
            //{ name: 'Help', link: '/about', label: 'About', icon: 'remove-red-eye' }
          ];
      }
    }, // Set menu
    onSignout() {
      this.$logout()
      this.user = {};
      this.isLoggedIn = false
      this.setMenu()
      this.$router.push('/login')
    }

  }

}
</script>

<style>
html {
  height: 100%;
}

body {
  background: #f8f9fa;
}


/*  #app {
    color: #2c3e50;
    font-family: Source Sans Pro, Helvetica, sans-serif;
    width: 100%;
  }
  */

a {
  cursor: pointer;
  cursor: hand;
}

.centertext {
  text-align: center;
}

.fade-enter-active {
  animation: fadeIn .4s;
}
.fade-leave-active {
  animation: fadeOut .4s;
}

.toolbar {
  background: #FFFFFF;
  color: #999ea4;
  align-items: center;
}

.logo {
  display: inline;
  font-size: 1.7em;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 600;
  vertical-align: middle;
}

.logo i {
  font-size: .5em;
  vertical-align: text-top;
}
.menubtn {
  padding-top: 0.6em !important;
  padding-bottom: 0.6em !important;
}
.title {
  background: #58B7FF;
  color: #fff;
  width: 100%;
  padding: .7em;
  font-size: 1.3em;
}


#header {
  position: fixed;
  z-index: 500;
  width: 100%;
  opacity: .94;
}

#contents {
  margin-top: 0 !important;
  padding-top: 3.7em;
}

#contents .column {
  padding-top: 0 !important;
  padding-right: 0 !important;
}

.page {
  height: 90vh;
}

@media all and (min-width: 0) and (max-width: 480px) {
  .toolbar span {
    display: none;
  }
}

@media only screen and (min-width: 1200px) {
  .ui.container {
    width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .ui.container {
    margin: 0 !important;
  }
}
</style>



