<template>
<div class="sidebar" :class="{'togglebar': !isMenu}">
  <slot> </slot>
</div>
</template>

<script>
'use strict';
export default {
  name: 'sidebar',
  created() {
      document.addEventListener("click", () => {this.onDocClick()} )
  },

  destroyed() {
    document.removeEventListener("click", () => {this.onDocClick()} )
  },

  data() {
    return {
      isMenu: false,

    }
  },
  methods: {
    onDocClick () {
      if (this.isMenu) {
        this.hide()
      }
    },
    show() {
      this.isMenu = true;
      //this.$el.classList.remove('togglebar')
    },
    hide() {
      this.isMenu = false;
      //this.$el.classList.add('togglebar')
  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sidebar {
  background: black;
  display: block;
  color: white;
  position: fixed;
  left: 0;
  z-index: 500;
  box-shadow: 0 0 20px rgba(34, 36, 38, .15);
  width: auto;
  height: 100%;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  padding-top: 1.5em;
}

.togglebar {
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}
</style>



